import React from 'react';
import Chart from 'chart.js';
import "./App.css";
import CurrencyTable from "./CurrencyTable"
import currencies from './Utils/currencyMap';
import { checkStatus, json } from './Utils/fetchUtils';

class CurrencyConverter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      rate: 0,
      baseAcronym: "USD",
      baseValue: 0,
      quoteAcronym: "GBP",
      quoteValue: 0,
      loading: false,
    };
    this.chartRef = React.createRef();
  }

  componentDidMount() {
    const { baseAcronym, quoteAcronym } = this.state;
    this.getRate(baseAcronym, quoteAcronym);
    this.getHistoricalRates(baseAcronym, quoteAcronym);
  }

  getRate = (base, quote) => {
    this.setState({ loading: true });
    fetch(`https://api.frankfurter.app/latest?from=${base}&to=${quote}`)
      .then(checkStatus)
      .then(json)
      .then((data) => {
        if (data.error) {
          throw new Error(data.error);
        }

        const rate = data.rates[quote];

        this.setState({
          rate,
          baseValue: 1,
          quoteValue: Number((1 * rate).toFixed(3)),
          loading: false,
        });
      })
      .catch((error) => console.error(error.message));
  };

  getHistoricalRates = (base, quote) => {
    const endDate = new Date().toISOString().split("T")[0];
    const startDate = new Date(new Date().getTime() - 30 * 24 * 60 * 60 * 1000)
      .toISOString()
      .split("T")[0];
    fetch(
      `https://api.frankfurter.app/${startDate}..${endDate}?from=${base}&to=${quote}`
    )
      .then(checkStatus)
      .then(json)
      .then((data) => {
        if (data.error) {
          throw new Error(data.error);
        }
        const chartLabels = Object.keys(data.rates);
        const chartData = Object.values(data.rates).map((rate) => rate[quote]);
        const chartLabel = `${base}/${quote}`;
        this.buildChart(chartLabels, chartData, chartLabel);
      })
      .catch((error) => console.error(error.message));
  };
  buildChart = (labels, data, label) => {
    const chartRef = this.chartRef.current.getContext("2d");
    if (typeof this.chart !== "undefined") {
      this.chart.destroy();
    }
    this.chart = new Chart(this.chartRef.current.getContext("2d"), {
      type: "line",
      data: {
        labels,
        datasets: [
          {
            label: label,
            data,
            fill: false,
            tension: 0,
            borderColor: "#7CC6FE",
          },
        ],
      },
      options: {
        responsive: true,
      },
    });
  };

  toBase(amount, rate) {
    return amount * (1 / rate);
  }

  toQuote(amount, rate) {
    return amount * rate;
  }

  convert(amount, rate, equation) {
    const input = parseFloat(amount);
    if (Number.isNaN(input)) {
      return "";
    }
    return equation(input, rate).toFixed(3);
  }

  changeBaseAcronym = (event) => {
    const baseAcronym = event.target.value;
    this.setState({ baseAcronym });
    this.getRate(baseAcronym, this.state.quoteAcronym);
    this.getHistoricalRates(baseAcronym, this.state.quoteAcronym);
  };

  changeBaseValue = (event) => {
    const quoteValue = this.convert(
      event.target.value,
      this.state.rate,
      this.toQuote
    );
    this.setState({
      baseValue: event.target.value,
      quoteValue,
    });
  };

  changeQuoteAcronym = (event) => {
    const quoteAcronym = event.target.value;
    this.setState({ quoteAcronym });
    this.getRate(this.state.baseAcronym, quoteAcronym);
    this.getHistoricalRates(this.state.baseAcronym, quoteAcronym);
  };

  changeQuoteValue = (event) => {
    const baseValue = this.convert(
      event.target.value,
      this.state.rate,
      this.toBase
    );
    this.setState({
      quoteValue: event.target.value,
      baseValue,
    });
  };

  render() {
    const { baseAcronym, baseValue, quoteAcronym, quoteValue, loading } =
      this.state;

    const currencyOptions = Object.keys(currencies).map((currencyAcronym) => (
      <option key={currencyAcronym} value={currencyAcronym}>
        {currencyAcronym}
      </option>
    ));

    return (
      <React.Fragment>
        <nav className="navbar navbar-light px-2 bg-light">
          <a className="navbar-brand mx-auto" href="#">
            <b>Coin Exchange</b>
          </a>
        </nav>
        <div className="d-md-flex px-2 h-md-100 align-items-center app-container">
          <div className="col-md-8 p-0 h-md-100">
            <form className="row justify-content-center">
              <div className="form-group col-md-5 mb-0">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <select
                      value={baseAcronym}
                      onChange={this.changeBaseAcronym}
                      className="form-control form-control-lg selector"
                      disabled={loading}
                    >
                      {currencyOptions}
                    </select>
                  </div>
                  <input
                    id="base"
                    className="form-control form-control-lg calculator"
                    value={baseValue}
                    onChange={this.changeBaseValue}
                    type="number"
                  />
                </div>
              </div>
              <div className="col-md-2 d-flex justify-content-center align-items-center">
                <h3>
                  <b>=</b>
                </h3>
              </div>
              <div className="form-group col-md-5 mb-0 second-currency">
                <div className="input-group">
                  <div className="input-group-prepend">
                    <select
                      value={quoteAcronym}
                      onChange={this.changeQuoteAcronym}
                      className="form-control form-control-lg selector"
                      disabled={loading}
                    >
                      {currencyOptions}
                    </select>
                  </div>
                  <input
                    id="quote"
                    className="form-control form-control-lg calculator"
                    value={quoteValue}
                    onChange={this.changeQuoteValue}
                    type="number"
                  />
                </div>
              </div>
            </form>
            <canvas className="p-4" ref={this.chartRef} />
          </div>
          <div className="col-md-4 p-0 h-md-100">
            <CurrencyTable />
          </div>
        </div>
        <footer className="footer mt-auto">
          <a className="foot-links" target="blank" href="https://offszn.io/">
            Portfolio
          </a>
          <a
            className="foot-links"
            target="blank"
            href="https://www.tiktok.com/@offsznsports?lang=en"
          >
            Social Media
          </a>
        </footer>
      </React.Fragment>
    );
  }
}

export default CurrencyConverter;